import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

export interface IRole {
  id?: string;
  name: string;
  description: string;
}

@Component({
  selector: 'app-role-list',
  templateUrl: './role-list.component.html',
  styleUrls: ['./role-list.component.scss']
})
export class RoleListComponent implements OnInit {

  public modalTitle: string;
  public form: FormGroup;
  public showModal = false;

  roles: IRole[] = [
    {
      id: '1',
      name: 'Manager',
      description: 'Lorem ipsum dolor sit amet consectetur adipisicing	',
    },
    {
      id: '2',
      name: 'Frontline',
      description: 'Lorem ipsum dolor sit amet consectetur adipisicing	',
    }
  ];

  constructor(
    private formBuilder: FormBuilder
  ) { }

  ngOnInit(): void {
    this.buildForm();
  }

  private buildForm(): void {
    this.form = this.formBuilder.group({
      id: '',
      name: ['', [Validators.required]],
      description: '',
    });
  }

  showAppForm(type: string, role?: IRole): void {
    if (type === 'add') {
      this.modalTitle = 'New Role';
    } else {
      this.modalTitle = `Edit ${role.name}`;
      this.form.setValue(role);
    }

    this.showModal = true;
  }

  closeModal(): void {
    this.form.reset();
    this.showModal = false;
  }


  submitForm(): void{
    this.form.markAllAsTouched();
    if (this.form.invalid) {
      return;
    }
    this.showModal = false;
    const {id, name, description }: IRole = this.form.value;

    if (id) {
      this.roles.map( x => {
        if (id === x.id) {
          x.name = name;
          x.description = description;
        }
      });
    } else {
      this.roles.push({
        id: new Date().getTime().toString(),
        name, description
      });
    }

    this.form.reset();
  }

  deleteApp(app: IRole): void {
    const sure = confirm('Are you sure?');
    if (sure) {
      this.roles = this.roles.filter( x => x.id !== app.id);
    }
  }
}
