import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

export interface IFunction {
  id?: string;
  name: string;
  code: string;
  description: string;
}
@Component({
  selector: 'app-function-group-view',
  templateUrl: './function-group-view.component.html',
  styleUrls: ['./function-group-view.component.scss']
})
export class FunctionGroupViewComponent implements OnInit {

  public modalTitle: string;
  public form: FormGroup;
  public showModal = false;

  functions: IFunction[] = [
    {
      id: '1',
      name: 'Change Shop Code',
      code: 'SB003',
      description: 'Lorem ipsum dolor sit amet consectetur adipisicing	',
    },
    {
      id: '2',
      name: 'PCD Aquisition',
      code: 'SB012',
      description: 'Lorem ipsum dolor sit amet consectetur adipisicing	',
    },
    {
      id: '3',
      name: 'MOB Profile',
      code: 'SB050',
      description: 'Lorem ipsum dolor sit amet consectetur adipisicing	',
    },
  ];
  constructor(
    private formBuilder: FormBuilder
  ) { }

  ngOnInit(): void {
    this.buildForm();
  }

  private buildForm(): void {
    this.form = this.formBuilder.group({
      id: '',
      name: ['', [Validators.required]],
      code: ['', [Validators.required]],
      description: '',
    });
  }

  showAppForm(type: string, fn?: IFunction): void {
    if (type === 'add') {
      this.modalTitle = 'New Function';
    } else {
      this.modalTitle = `Edit ${fn.name}`;
      this.form.setValue(fn);
    }

    this.showModal = true;
  }

  closeModal(): void {
    this.form.reset();
    this.showModal = false;
  }


  submitForm(): void{
    this.form.markAllAsTouched();
    if (this.form.invalid) {
      return;
    }
    this.showModal = false;
    const {id, name, code, description }: IFunction = this.form.value;

    if (id) {
      this.functions.map( x => {
        if (id === x.id) {
          x.name = name;
          x.code = code;
          x.description = description;
        }
      });
    } else {
      this.functions.push({
        id: new Date().getTime().toString(),
        code, name, description
      });
    }

    this.form.reset();
  }

  deleteApp(fn: IFunction): void {
    const sure = confirm('Are you sure?');
    if (sure) {
      this.functions = this.functions.filter( x => x.id !== fn.id);
    }
  }

}
