import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

export interface IFunctionGroup {
  id?: string;
  name: string;
  description: string;
}

@Component({
  selector: 'app-function-group-list',
  templateUrl: './function-group-list.component.html',
  styleUrls: ['./function-group-list.component.scss']
})
export class FunctionGroupListComponent implements OnInit {

  public modalTitle: string;
  public form: FormGroup;
  public showModal = false;

  functionGroups: IFunctionGroup[] = [
    {
      id: '1',
      name: 'PTC-Manager',
      description: 'Lorem ipsum dolor sit amet consectetur adipisicing	',
    },
    {
      id: '2',
      name: 'IBS-Frontline',
      description: 'Lorem ipsum dolor sit amet consectetur adipisicing	',
    }
  ];

  constructor(
    private formBuilder: FormBuilder
  ) { }

  ngOnInit(): void {
    this.buildForm();
  }

  private buildForm(): void {
    this.form = this.formBuilder.group({
      id: '',
      name: ['', [Validators.required]],
      description: '',
    });
  }

  showAppForm(type: string, functionGroup?: IFunctionGroup): void {
    if (type === 'add') {
      this.modalTitle = 'New Function Group';
    } else {
      this.modalTitle = `Edit ${functionGroup.name}`;
      this.form.setValue(functionGroup);
    }

    this.showModal = true;
  }

  closeModal(): void {
    this.form.reset();
    this.showModal = false;
  }


  submitForm(): void{
    this.form.markAllAsTouched();
    if (this.form.invalid) {
      return;
    }
    this.showModal = false;
    const {id, name, description }: IFunctionGroup = this.form.value;

    if (id) {
      this.functionGroups.map( x => {
        if (id === x.id) {
          x.name = name;
          x.description = description;
        }
      });
    } else {
      this.functionGroups.push({
        id: new Date().getTime().toString(),
        name, description
      });
    }

    this.form.reset();
  }

  deleteApp(app: IFunctionGroup): void {
    const sure = confirm('Are you sure?');
    if (sure) {
      this.functionGroups = this.functionGroups.filter( x => x.id !== app.id);
    }
  }

}
