import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

export interface Iapi {
  id?: string;
  task: string;
  method: string;
  endpoint: string;
  description: string;
  parameters: string;
}

@Component({
  selector: 'app-api-list',
  templateUrl: './api-list.component.html',
  styleUrls: ['./api-list.component.scss']
})
export class ApiListComponent implements OnInit {

  public modalTitle: string;
  public form: FormGroup;
  public showModal = false;

  apis: Iapi[] = [
    {
      id: '1',
      task: 'ADD_USER',
      method: 'POST',
      endpoint: 'http://www.svc-api.com/create/user',
      description: 'Lorem ipsum dolor sit amet consectetur adipisicing	',
      parameters: 'name,roles,org'
    },
    {
      id: '2',
      task: 'DELETE_ROLE',
      method: 'DELETE',
      endpoint: 'http://www.svc-api.com/create/delete',
      description: 'Lorem ipsum dolor sit amet consectetur adipisicing	',
      parameters: 'id'
    }
  ];

  constructor(
    private formBuilder: FormBuilder
  ) { }

  ngOnInit(): void {
    this.buildForm();
  }

  private buildForm(): void {
    this.form = this.formBuilder.group({
      id: '',
      task: ['', [Validators.required]],
      method: ['', [Validators.required]],
      endpoint: ['', [Validators.required]],
      description: '',
      parameters: '',
    });
  }

  showAppForm(type: string, api?: Iapi): void {
    if (type === 'add') {
      this.modalTitle = 'New API';
    } else {
      this.modalTitle = `Edit ${api.task}`;
      this.form.setValue(api);
    }

    this.showModal = true;
  }

  closeModal(): void {
    this.form.reset();
    this.showModal = false;
  }


  submitForm(): void{
    this.form.markAllAsTouched();
    if (this.form.invalid) {
      return;
    }
    this.showModal = false;
    const {id, task, method, endpoint, description, parameters}: Iapi = this.form.value;

    if (id) {
      this.apis.map( x => {
        if (id === x.id) {
          x.task = task;
          x.method = method;
          x.endpoint = endpoint;
          x.description = description;
          x.parameters = parameters;
        }
      });
    } else {
      this.apis.push({
        id: new Date().getTime().toString(),
        task, method, endpoint, description, parameters
      });
    }

    this.form.reset();
  }

  deleteApp(app: Iapi): void {
    const sure = confirm('Are you sure?');
    if (sure) {
      this.apis = this.apis.filter( x => x.id !== app.id);
    }
  }
}
