import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { ApplicationComponent } from './application/application.component';
import { SharedModule } from '../../shared/shared.module';
import { ApplicationViewComponent } from './application-view/application-view.component';
import { FunctionGroupViewComponent } from './function-group-view/function-group-view.component';
import { ApplicationRoleViewComponent } from './application-role-view/application-role-view.component';
import { FunctionViewComponent } from './function-view/function-view.component';
import { ReactiveFormsModule } from '@angular/forms';
import { ApiListComponent } from './api-list/api-list.component';
import { RoleListComponent } from './role-list/role-list.component';
import { FunctionGroupListComponent } from './function-group-list/function-group-list.component';


const routes: Routes = [
  { path: '', component: ApplicationComponent },
  { path: ':applicationid', component: ApplicationViewComponent },
  { path: ':applicationid/approle/:approleid', component: ApplicationRoleViewComponent },
  { path: ':applicationid/functiongroup/:functiongroupid', component: FunctionGroupViewComponent },
  { path: ':applicationid/functiongroup/:functiongroupid/function/:functionid', component: FunctionViewComponent }

];

@NgModule({
  declarations: [
    ApplicationComponent,
    ApplicationViewComponent,
    FunctionGroupViewComponent,
    ApplicationRoleViewComponent,
    FunctionViewComponent,
    ApiListComponent,
    RoleListComponent,
    FunctionGroupListComponent
  ],
  imports: [
    CommonModule,
    SharedModule,
    ReactiveFormsModule,
    RouterModule.forChild(routes)
  ]
})
export class ApplicationModule { }
